<template>
  <div class="homes">
    <van-sticky>
      <van-nav-bar class="title" title="FEILU">
        <van-icon class="iconfont icon-lingdang" slot="left" />
        <div slot="right">
          <van-icon class="iconfont icon-phone" />
          <van-icon class="iconfont icon-sousuo" />
        </div>
      </van-nav-bar>
    </van-sticky>

    <van-row>
      <van-image width="10rem" :src="imgs[0]" />
    </van-row>
    <van-row>
      <van-image width="10rem" :src="imgs[1]" />
    </van-row>
    <van-image width="10rem" :src="imgs[2]" />
    <van-image width="10rem" :src="imgs[3]" />
    <van-grid :border="false" :column-num="2">
      <van-grid-item v-for="(item,index) in list" :key="index" :url="item" icon="photo-o" text="图片">
        <van-image width="100%" :src="item" />
      </van-grid-item>
    </van-grid>
    <van-grid :border="false" :column-num="2">
      <van-grid-item
        v-for="(item,index) in list2"
        :key="index"
        :url="item"
        icon="photo-o"
        text="图片"
      >
        <van-image width="100%" :src="item" />
      </van-grid-item>
    </van-grid>
    <div class="list_bottom">
      <van-row>
        <span class="item_left">Flash Sale</span>
        <van-count-down class="times" :time="time">
          <template v-slot="timeData">
            <em>END IN</em>
            <span class="item">{{ timeData.hours }}</span>
            <em>:</em>
            <span class="item">{{ timeData.minutes }}</span>
            <em>:</em>
            <span class="item">{{ timeData.seconds }}</span>
          </template>
        </van-count-down>
      </van-row>
      <van-grid :gutter="5" :border="false" :column-num="4">
        <van-grid-item
          v-for="(item,index) in list3"
          :key="index"
          :url="`${baseUrl}${item.image}`"
          icon="photo-o"
          text="图片"
        >
          <div class="text" slot="text">
            <div>{{ item.salePriceTxt }}</div>
            <del>{{ item.marketPriceTxt }}</del>
          </div>
          <div slot="icon">
            <i class="list_icon">{{ `${item.salePrice/item.marketPrice*100}% off` }}</i>
            <van-image width="100%" :src="`${baseUrl}${item.image}`" />
          </div>
        </van-grid-item>
      </van-grid>
      <van-tabs
        v-model="active"
        animated
        lazy-render
        swipe-threshold="5"
        title-active-color="#000000"
        title-inactive-color="#666666"
        swipeable
      >
        <!-- <lazy-component> -->
        <van-tab
          :change="getLIstLazy"
          v-for="(name,i) in listName"
          :key="i"
          title-style="font-size:0.2rem"
          :title="name"
        >
          <van-list
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="getLIstLazy"
          >
            <van-grid :gutter="5" :border="false" :column-num="2">
              <van-grid-item
                v-for="(item,index) in list4.list"
                :key="index"
                :url="`${baseUrl}${item.image}`"
                icon="photo-o"
                text="图片"
              >
                <van-image slot="icon" lazy-load width="100%" :src="`${baseUrl}${item.image}`" />
                <div class="morey" slot="text">
                  <div>{{ item.salePriceTxt }}</div>
                </div>
              </van-grid-item>
            </van-grid>
          </van-list>
        </van-tab>

        <!-- </lazy-component> -->
      </van-tabs>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  components: {},
  data() {
    return {
      imgs: [
        require("./../../assets/imgs/15529606762771619905.jpg"),
        require("../../assets/imgs/1552640304190970081.jpg"),
        require("../../assets/imgs/1552905829800983027.jpg"),
        require("../../assets/imgs/15529047253037457965.jpg")
      ],
      time: 30 * 60 * 60 * 1000,
      baseUrl: "http://www.images.geyunjie.com/imgs/",
      // 真是项目只需要一个list
      list: [
        "1552904838461490989.jpg",
        "15529048982185809916.jpg",
        "1552904903488529335.jpg",
        "155290486836959652.jpg",
        "15529048623739862115.jpg",
        "15529048432825868780.jpg"
      ],
      list2: [
        "15529049533718568136.jpg",
        "1552904957260482913.jpg",
        "1552908388504854855.jpg",
        "15529083931435181614.jpg"
      ],
      list3: [],
      list4: {
        totalRow: 3,
        pageNumber: 1,
        firstPage: true,
        lastPage: true,
        totalPage: 1,
        pageSize: 10,
        list: []
      },
      listName: ["Treading Now", "Blouses", "Dresses", "Bottoms", "Swimwear"],
      active: "1",
      loading: false,
      finished: false,
      number: 0,
      container: null
    };
  },
  methods: {
    getImages() {
      this.list = this.list.map(item => `${this.baseUrl}${item}`);
      this.list2 = this.list2.map(item => `${this.baseUrl}${item}`);
    },
    async getGoodList() {
      const { data } = await this.$request({
        url: "goods_lower",
        methods: "get"
      });
      this.list3 = data.data;
    },
    async getLIstLazy() {
      // this.loading = true;
      const { data } = await this.$request({
        url: "list",
        methods: "get"
        // parmas: {
        //   pageSize: this.list4.pageSize || null
        // }
      });

      console.log(this.list4);
      if (this.list4.list.length) {
        console.log("aaaa");

        this.list4.list.push(...data.data.list);
      } else {
        this.list4.list = data.data.list;
      }
      this.loading = false;
      this.number++;
      // 数据全部加载完成
      if (this.number >= 10) {
        this.finished = true;
      }
    }
  },
  created() {
    this.getImages();
    this.getGoodList();
  }
};
</script>

<style lang="less" scoped>
.homes {
  ::v-deep .van-sticky--fixed {
    top: 0;
    right: 0;
    left: 50%;
    z-index: 99;
    width: 10rem;
    transform: translateX(-50%);
  }
}
.title {
  font-size: 2rem;
  .iconfont {
    margin: 0 0 0 0.3rem;
    font-size: 0.5rem;
    color: #000;
  }
  .iconfont:nth-child(1) {
    margin: 0;
  }
}

.van-row {
  .van-image {
    display: block;
  }
}
.van-grid {
  margin: 0;
  padding: 0;
  /deep/.van-grid-item__content {
    margin: 0;
    padding: 0;
    .van-image {
      margin: 0;
    }
  }
}
.list_bottom {
  padding: 0 0.4rem;
}
.van-count-down {
  float: right;
}
.item_left {
  float: left;
  height: 1rem;
  line-height: 1rem;
}
.item {
  display: inline-block;
  width: 22px;
  margin-right: 5px;
  margin: 0 auto;
  margin-top: 0.5rem;
  transform: translate(0, -50%);
  color: #fff;
  font-size: 12px;
  text-align: center;
  background-color: #000;
}
em {
  margin: 0 auto;
  margin-top: 0.5rem;
  transform: translate(0, -50%);
  display: inline-block;
  margin: 0 0.1rem;
  font-style: normal;
  font-size: 0.2rem;
}
// flex更简单
.list_icon {
  position: absolute;
  background-color: antiquewhite;
  z-index: 10;
  font-size: 0.3rem;
  width: 0.6rem;
  height: 0.6rem;
  text-align: center;
  line-height: 0.3rem;
  padding: 0.2rem 0.1rem;
}
.list_icon::after {
  content: "";
  height: 0;
  width: 0;
  position: absolute;
  border-top: 0.15rem solid antiquewhite;
  border-bottom: 0.15rem solid transparent;
  border-left: 0.4rem solid transparent;
  border-right: 0.4rem solid transparent;
  top: 1rem;
  left: 0.02rem;
}
.text {
  font-size: 0.2rem;
  text-align: left;
  width: 100%;
  margin-bottom: 1rem;
  div {
    color: red;
  }
}
.morey {
  text-align: left;
  margin: 0.1rem;
  width: 100%;
  font-size: 0.2rem;
  color: red;
}
</style>
